.product-item {

}

.product-item .card{
    min-height: 534px;
}

.product-item .card h3 {
    font-size: 1.5rem;
}

.product-item-title {
    text-align: left;
    color: #A93133;
}

.product-item-desc {
    text-align: left;
    min-height: 96px;
}

.product-item-props {
    text-align: right;
}
.product-item-price {

}

.product-item-counter {

}

.product-item-counter .minus {
    cursor: pointer;
}

.product-item-counter .with-border {
    display: inline-block;
    min-width: 25px;
    margin: 0 .1rem;
    padding: .25rem .5rem;
    border: 1px solid transparent;
    border-color: #6c757d;
    border-radius: .2rem;
}

.product-item-counter .counter {
    
}

.product-item-counter .plus {
    cursor: pointer;
}

.category-name {
    text-align: left;
    font-size: 2rem;
}

.category {
    cursor: pointer;
    background-color: black;
    border-color: white;
    color: white;
    outline: none;
}

.category:focus {
    box-shadow: none;
}

.category.active {
    color: #212529;
    background-color: #A93133;
    border-color: #A93133;
  }

.category-name {
    color: #A93133;
}

.card-body {
    background-color: black;
    color: white;
}

.card-body .text-muted {
    color: white;
}

