.Footer h5 {
    color: #A93133;
}
.Footer {
    color: #fff;
}
.Footer .navigation {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}
@media screen and (max-width: 435px) {
    .Footer .navigation {
        display: block;
    }
}
.Footer .navigation a {
    position: absolute;
    bottom: 5px;
    height: 48px;
    color: #fff;
    background-color: #000;
}
.Footer .navigation a:focus {
    box-shadow: none;
}
.Footer .navigation a i {
    font-size: 2rem;
}
.Footer .navigation .go-top-button {
    right: 5px;
}
.Footer .navigation .call-button {
    left: 5px;
}